.contentWrap{
  display: flex;
  margin-bottom: 20px;
}

.btn {
  margin-bottom: 20px;
  margin-left: 40px;
  min-width: 100px;
  height: 30px;
  font-family: 'Raleway';
  font-size: 18px;
  font-weight: 700;
  border-radius: 30px;
  background-color: #525b8f;
  border: 1px solid transparent;
  color: #ffffff;
  cursor: pointer;
}

.btn:hover, .btn:focus {
  color:#525b8f;
  background-color: #ffffff;
}

.infoWrap {
  margin-left: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: left;
}

.info {
  font-weight: 700;
  color: #ffffff;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 20px;
}

.rating {
  composes: info;
  display: inline-block;
  font-size: 25px;
  margin-left: 5px;
 color:#757fb8;
}


.description {
  margin-left: 10px;
  color: #bbb9b9;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.16;
  text-transform: none;
}

.movieTitle {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 30px;
  height: 40px;
  padding-top: 10px;
  /* background-color: #78797ed5;
  border-radius: 30px; */
  font-weight: 700;
  font-size: 35px;
  line-height: 1.16;
  letter-spacing: 0.03em;
  text-align: center;
  color: #ffffff;
}

.description {
  text-align: left;
}

.image {
  margin-right: 30px;
  margin-bottom: 0;

  max-width: 320px;
  height: 400px;
  border-radius: 2px;
  object-fit: cover;
   box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.title {
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
color:#757fb8;
}

.linkWrap{
display: flex;
justify-content: space-evenly;
margin-bottom: 20px;
background-color: #78797ed5;
border-radius: 30px;
}

.link{
display: inline-block;
height: 40px;
padding-top: 10px;
font-weight: 700;
font-size: 20px;
color: #000000;
text-decoration: none;
text-align: center;
/* border-radius: 30px;
border: 1px solid #525b8f; */

}

.activeLink {
  composes: link;
  color: #ffffff;
}

.link:hover, .link:focus{
  color: #ffffff;
}


